<template>
  <body>
    <div v-if="isInputComplete">
      <v-if v-if="isInputOngoing" name="ongoing">
        <div class="split left" id="Input" v-on:keyup.enter="inputParams">
          <input
            name="dbase"
            ref="dbase"
            value="Type in your database type, i.e. MySQL"
          />
          <br />
          <input
            name="dbHost"
            ref="dbHost"
            value="Type in your host address for connecting"
          />
          <br />
          <input
            name="dbPort"
            ref="dbPort"
            value="Type in your port for connecting, i.e. 3306"
          />
          <br />
          <input
            name="dbName"
            ref="dbName"
            value="Type in the name of your database"
          />
          <br />
          <input
            name="dbUser"
            ref="dbUser"
            value="Type in the username required for connecting"
          />
          <br />
          <h4>Type in your password in the field below</h4>
          <input
            name="dbPassword"
            ref="dbPassword"
            value="Type in the password as well"
            type="password"
          />
          <br />
          <br />
          <h4>Select scan option for finding references in your database</h4>
          <span
            class="options"
            v-for="(option, index) in scan_options"
            :key="index"
          >
            <input
              :id="option"
              :value="option"
              name="table"
              type="checkbox"
              class="checkbox_option"
              v-model="selected_option"
              v-on:click="checkOne()"
            />
            {{ option }}
          </span>
          <br />
          <br />
          <button v-on:click.prevent="inputParams">
            Run scan of my database!
          </button>
        </div>
      </v-if>
      <v-else name="imgSesam">
        <div class="split left" v-if="isScanResponse">
          <img
            class="sesam_img"
            src="https://sesam.io/images/Hub1.png"
            alt="sesam_img"
          />
        </div>
      </v-else>
      <v-if v-if="isScanResponse" name="scan">
        <div class="split right" id="Response">
          <h2>Pick your tables to model</h2>
          <div class="checkboxes">
            <li v-for="(table, index) in rows['result']" :key="index">
              <input
                :id="table"
                :value="table"
                name="table"
                type="checkbox"
                class="checkbox"
                v-model="selected_tables"
              />
              {{ table }}
            </li>
          </div>
          <button v-on:click="isSelectComplete">Selection Complete</button>
        </div>
      </v-if>
      <v-else v-if="isBufferActive" name="buffer">
        <div class="split right" id="Response">
          <span v-html="bufferIcon()"></span>
        </div>
      </v-else>
    </div>
    <div>
      <component
        :selected_tables="selected_tables"
        class="component"
        v-if="isSelectionComplete"
        :is="nextComponent"
      ></component>
    </div>
  </body>
</template>

<script>
import api from "../api";
import MetaComponent from "./Meta";
export default {
  name: "Input",
  data: () => {
    return {
      isScanResponse: false,
      isBufferActive: false,
      isSelectionComplete: false,
      isInputOngoing: true,
      isSesamInt: false,
      isInputComplete: true,
      rows: "{{tables}}",
      scan_options: [
        "No references",
        "Foreign Key references",
        "Index references",
      ],
      selected_option: [],
      selected_tables: [],
      nextComponent: "MetaComponent",
    };
  },
  components: {
    MetaComponent,
  },
  methods: {
    async inputParams() {
      let dbase = this.$refs.dbase.value;
      let dbHost = this.$refs.dbHost.value;
      let dbPort = this.$refs.dbPort.value;
      let dbName = this.$refs.dbName.value;
      let dbUser = this.$refs.dbUser.value;
      let dbPassword = this.$refs.dbPassword.value;
      let option = this.selected_option;
      this.isBufferActive = true;
      this.isSesamInt = true;
      await fetch("http://localhost:5000/connectors", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          dbase: dbase,
          dbHost: dbHost,
          dbPort: dbPort,
          dbName: dbName,
          dbUser: dbUser,
          dbPassword: dbPassword,
          option: option,
        }),
      });
      this.$refs.dbase.value = [];
      this.$refs.dbHost.value = [];
      this.$refs.dbPort.value = [];
      this.$refs.dbName.value = [];
      this.$refs.dbUser.value = [];
      this.$refs.dbPassword.value = [];
      this.scanResponse();
    },
    bufferIcon() {
      return '<img src="https://media.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy.gif" alt="Loading GIF">';
    },
    checkOne() {
      this.selected_option = [];
    },
    scanResponse() {
      api.getResource("/scan_db").then((data) => {
        // eslint-disable-next-line no-console
        //console.log(data)
        if (data != null && data != "") {
          this.isScanResponse = true;
          this.isBufferActive = false;
          this.isInputOngoing = false;
          this.rows = data;
        }
      });
    },
    isSelectComplete() {
      this.isSelectionComplete = true;
      this.isInputComplete = false;
    },
  },
};
</script>

