<template>
  <body>
    <div v-if="preSelection" name="pre">
      <br />
      <button v-on:click="isGlobals">I want Global pipes</button>
      <button v-on:click="isNoGlobals">I dont want Global pipes</button>
    </div>
    <div v-if="wantGlobals" name="globals">
      <br />
      {{ selected_tables }}
    </div>
    <div v-if="dontWantGlobals" name="no_globals">
      <br />
      <h2>The following tables will be created in your Sesam instance.</h2>
      <br />
      <p>{{ selected_tables }}</p>
      <br />
      <h2>-----</h2>
      <br />
      <h2 class="h2_special">
        In order for me to make the tables please provide the following
        information.
      </h2>
      <br />
      <div v-on:keyup.enter="inputParams">
        <input
          name="sesamBaseURL"
          ref="sesamBaseURL"
          value="Type in your SESAM base url, i.e. "
        />
        <br />
        <h3>Type in your SESAM JWT in the field below</h3>
        <input
          name="sesamJWT"
          ref="sesamJWT"
          value="Type in the password as well"
          type="password"
        />
        <br />
        <button v-on:click.prevent="inputParams">Model my tables</button>
      </div>
    </div>
    <div v-if="postSelection">
      <v-if v-if="isSesamResponse" name="sesam_response">
        <div class="center">
          <br />
          <br />
          <h2>{{ result["sesam_result"] }}</h2>
          <h2>Follow the below link to look at your new data flows!</h2>
          <br />
          <a href="https://portal.sesam.io/dashboard">Sesam Portal</a>
        </div>
      </v-if>
      <v-else name="buffer" v-if="isBufferActive">
        <div class="center">
          <span v-html="bufferIcon()"></span>
        </div>
      </v-else>
    </div>
  </body>
</template>
  
  <script>
import api from "../api";
export default {
  name: "Meta",
  data: () => {
    return {
      preSelection: true,
      postSelection: false,
      isSesamResponse: false,
      isBufferActive: false,
      wantGlobals: false,
      dontWantGlobals: false,
      result: "{{result}}",
    };
  },
  props: ["selected_tables"],
  methods: {
    isGlobals() {
      this.preSelection = false;
      this.wantGlobals = true;
    },
    isNoGlobals() {
      this.preSelection = false;
      this.dontWantGlobals = true;
    },
    bufferIcon() {
      return '<img class="image" src="https://media.giphy.com/media/ftAyb0CG1FNAIZt4SO/giphy.gif" alt="Loading GIF">';
    },
    async inputParams() {
      let sesamBaseURL = this.$refs.sesamBaseURL.value;
      let sesamJWT = this.$refs.sesamJWT.value;
      let tables = this.selected_tables;
      this.isBufferActive = true;
      this.wantGlobals = false;
      this.dontWantGlobals = false;
      this.postSelection = true;
      await fetch("http://localhost:5000/model_data", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          sesamBaseURL: sesamBaseURL,
          sesamJWT: sesamJWT,
          tables: tables,
        }),
      });
      this.$refs.sesamBaseURL.value = [];
      this.$refs.sesamJWT.value = [];
      this.scanResponse();
    },
    scanResponse() {
      api.getResource("/sesam_response").then((data) => {
        if (data != null && data != "") {
          //eslint-disable-next-line no-console
          console.log("testing this...");
          //eslint-disable-next-line no-console
          console.log(data);
          this.isBufferActive = false;
          this.isSesamResponse = true;
          this.result = data;
        }
      });
    },
  },
};
</script>
  
<style scoped>
a {
  font-size: 14px;
}

p {
  font-size: 14px;
}

.center {
  padding: 10%;
}

body {
  width: 100%;
}
