import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import Input from './components/Input'
import Meta from './components/Meta'

Vue.config.productionTip = false
Vue.use(VueRouter)

const routes = [{
  path: '/scan_db',
  component: Input
}, {
  path: '/sesam_response',
  component: Meta
}]

const router = new VueRouter({
  routes,
  mode: 'history'
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
});